@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*, html {
  font-family: 'Montserrat', sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  }

  body {
    background-color: whitesmoke;
  }

.Jumbotron {
  align-items: center;
  background-color: whitesmoke;
  color: whitesmoke;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: auto;
  background-image: url(/gigstarter/static/media/back2.7c1f7879.jpg);
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: left top;
  background-size: 100vw;
  height: 100vh;
  padding-top:10rem;
  text-align: center;
  top: 0;
}

.Jumbotron__buttons {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-evenly;
}

.Jumbotron__buttons__button {
  background-color: whitesmoke;
  border: none;
  border-radius: 10px;
  font-weight: bold;
  margin: 1rem;
  padding: 1rem 1.5rem;
  transition: all .5s ease-in-out;
}

.Jumbotron__buttons__button:hover {
  background: #39322c;
  color: whitesmoke;
  cursor: pointer;
}

.Jumbotron__heading1 {
  font-size: 4em;
  padding:  0 1.5rem;
}

.Jumbotron__heading2 {
  font-family: 'Open Sans', sans-serif;
  font-size: 1.5em;
  font-style: italic;
  margin: 2rem auto;
  width: 600px;
}

@media (max-width: 1046px) {

  .Jumbotron {
    justify-content: flex-start;
    padding-top: 14rem;
  }
  
}

@media (max-width: 730px) {
  .Jumbotron {
    background-position: 25%;
    background-size: cover;
    justify-content: flex-start;
    height: 100%;
    padding-top: 9rem;
    padding-bottom: 8rem;
    width: 100vw;
  }
  
  .Jumbotron__heading1 {
    font-size: 3em;
  }

  .Jumbotron__heading2 {
    width: 90vw;
  }

}
.Section1 {
    display: flex;
    flex-direction: row;
    align-items: center;
    flex-wrap: wrap;
    padding: 4rem 0;
    text-align: center;
  }
  
  .Section1__career {
    color: white;
    background-image: url(/gigstarter/static/media/greenind.3797155a.svg);
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 80vh;
    min-height: 530px;
    margin: auto;
    max-width: 50%;
    min-width: 530px;
    padding: 0 5.5rem 5.5rem 5.5rem;
  }
  
  .Section1__career__heading {
    font-size: 2.5rem;
    font-weight: 400;
    margin-bottom: .5rem;
    margin-left: -2rem;
    margin-top: 2rem;
  }
  
  .Section1__career__description {
    font-size: 1.1rem;
    letter-spacing: 2px;
    line-height: 1.5rem;
    margin-left: -2rem;
    margin-top: 0;
    width: 75%;
  }
  
  .Section1__steps {
    align-self: center;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    font-size: 1.2rem;
    justify-content: center;
    padding: 1rem 3rem;
    margin: auto;
    width: 50%;
  }
  
  .Section1__steps__step {
    margin: 1.5rem .5rem;
    min-width: 150px;
    width: 20%;
  }

  .Section1__steps__step__header {
    font-weight: 300;
    margin: .5rem;
  }

@media (max-width: 640px) {

  .Section1__career {
    background-image: none;
    color: black;
    height: auto;
    justify-content: flex-start;
    min-height: 0px;
    min-width: 100%;
    padding: 0 1rem 0 1rem;
    width: 100%;
  }

  .Section1__career__heading,
  .Section1__career__description {
    margin-left: 0;
    margin-bottom: 1.5rem
  }
  
}

.Section2 {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-self: center;
    width: 100%;
    height: 100%;
    padding: 4rem 0;
}

.Section2__economy {
    display: flex;
    flex-direction: column;
    align-self: center;
    margin: 1.5rem;
    text-align: center;
}

.Section2__economy__header {
    font-weight: 400;
    margin-bottom: 2rem;
}

.Section2__economy__list {
    border: 1px solid #bcbdc0;
    border-radius: .5rem;
    display: flex;
    flex-direction: column;
    align-self: center;
}

.Section2__economy__list__item {
    padding: 1rem 2rem;
    font-size: 1.8rem;
}

.Section2__economy__list--blue {
    background-color: #b8daff;
    color: #1436ca;
}

.Section2__economy__list--purple {
    background-color: #d7d8dc;
    color: #3b3d39;
}

.Section2__economy__list--green {
    background-color: #c3e5ca;
    color: #355331;
}

.Section2__video {
    width: 56%;
    position: relative;
    padding: 0 10rem;
    padding-bottom: 29.38%;
    padding-top: 25px;
    height: 0;
    margin: 1.5rem;
}

.Section2__video__iframe {
    border: 0;
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
}
.Section3 {
    display: flex;
    flex-direction: row;
    align-items: center;
    flex-wrap: wrap-reverse;
    padding: 4rem 0;
    text-align: center;
  }
  
  .Section3__bigPicture {
    color: white;
    background-image: url(/gigstarter/static/media/orangeind.3cd45c17.svg);
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 80vh;
    min-height: 530px;
    margin: auto;
    max-width: 50%;
    min-width: 530px;
    padding: 5.5rem;
  }
  
  .Section3__bigPicture__heading {
    font-size: 2rem;
    font-weight: 400;
    margin-bottom: .5rem;
    margin-left: -2rem;
    margin-top: 4rem;
  }
  
  .Section3__bigPicture__description {
    font-size: 1.1rem;
    letter-spacing: 2px;
    line-height: 1.5rem;
    margin-left: -2rem;
    margin-top: 0;
    width: 75%;
  }
  
  .Section3__steps {
    align-self: center;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    font-size: 1.2rem;
    justify-content: center;
    padding: 1rem 3rem;
    margin: auto;
    width: 50%;
  }
  
  .Section3__steps__step {
    margin: 1.5rem .5rem;
    min-width: 150px;
    width: 20%;
  }

@media (max-width: 640px) {

  .Section3 {
    padding: 0 0 4rem 0;
  }

  .Section3__bigPicture {
    background-image: none;
    color: black;
    height: auto;
    justify-content: flex-start;
    min-height: 0px;
    min-width: 100%;
    padding: 1.5rem 1rem 0 1rem;
    width: 100%;
  }

  .Section3__bigPicture__heading,
  .Section3__bigPicture__description {
    margin-left: 0;
  }
  
}

.Section4 {
    font-weight: 300;
    padding: 4rem 0;
    text-align: center;
}

.Section4__header {
    margin-bottom: 2rem;
}

.Section4__description {
    font-size: 1.3rem;
}

.Section4__ctas {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin-top: 2rem; 
}

.Section4__ctas__cta {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.Section4__ctas__cta__button {
    border: none;
    border-radius: .25rem;
    color: white;
    font-size: 1.3rem;
    margin-top: 1rem;
    padding: .5rem 1rem;
    width: 256px;
    transition: all 200ms ease-in-out;
}

.Section4__ctas__cta__button:hover {
    cursor: pointer;
}

.Section4__ctas__cta__button--blue {
    background-color: #007aff;
}

.Section4__ctas__cta__button--blue:hover {
    background-color: #007bff9d;
}

.Section4__ctas__cta__button--green {
    background-color: #27a844;
}

.Section4__ctas__cta__button--green:hover {
    background-color: #27a843a1;
}

.Section4__ctas__or {
    font-size: 4rem;
    color: rgb(197, 197, 197);
    margin: 2rem;
}

@media(max-width: 730px) {
    
    .Section4__ctas__cta__emoji {
        width: 100px;
    }

    .Section4__ctas__cta__button {
        width: 125px;
    }

    .Section4__ctas__or {
        font-size: 2rem;
    }

}

@media(min-width: 848px) {
  
  .nav-container {
    align-items: center;
    background-color: #f5f5f5;
    color: #141414;
    display: flex;
    flex-direction: row;
    font-size: 1.3rem;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 1;
    }
    
    .nav-right, .nav-left {
      line-height: 5rem;
      padding: 0 4rem;
      width: 50%;
    }
    
    .nav-left__item {
        display: inline-block;
        height: 100%;
        margin-right: 1.5rem;
        transition: color ease-in-out .5s;
    }
    
    .nav-right {
      align-items: center;
      display: flex;
      flex-direction: row;
      justify-content: flex-end;
    }
    
    .nav-right__item {
      margin-right: 1.5rem;
      transition: color ease-in-out .5s;
    }
    
    .nav-right__last-item {
      padding-right: 0;
      transition: color ease-in-out .5s;
    }
    
    .nav-right__item:hover,
    .nav-right__last-item:hover,
    .nav-left__item:hover {
      color: #39322c;
      cursor: pointer;
    }
    
    .nav-link {
      background-color: none;
      color: #141414;
      text-decoration: none;
    }

    /*Hide Burger-Menu for large media query */
    .nav-container-mobile {
        display: none;
    }

  }
  
  /* Position and sizing of burger button */
  @media(max-width: 847px) {
    
    .nav-container-mobile {
      position: fixed;
      z-index: 1;
      background: black;
      height: 6rem;
      width: 100vw;
    }
    
    .menu-item {
      color: #141414;
      text-decoration: none;
      transition: all 1s ease-in-out;
      padding: .5rem 0;
    }
    
    .menu-item:hover {
      color:#39322c;
      cursor: pointer;
    }
    
    .menu-item:focus {
      outline: none;
    }
    
    .bm-burger-button {
      position: relative;
      height: 30px;
      left: 2rem;
      top: 2rem;
      width: 2rem;
    }
    
    /* Color/shape of burger icon bars */
    .bm-burger-bars {
      background: #f5f5f5;
    }
    
    /* Color/shape of burger icon bars on hover*/
    .bm-burger-bars-hover {
      background: #39322c;
    }
    
    /* Position and sizing of clickable cross button */
    .bm-cross-button {
      height: 24px;
      width: 24px;
    }
    
    /* Color/shape of close button cross */
    .bm-cross {
      background: black;
    }
    
    /*
    Sidebar wrapper styles
    Note: Beware of modifying this element as it can break the animations - you should not need to touch it in most cases
    */
    .bm-menu-wrap {
      position: fixed;
      height: 100%;
    }
    
    /* General sidebar styles */
    .bm-menu {
      background: #ffffff;
      padding: 2.5em 1.5em 0;
      font-size: 1.15em;
      color: black;
    }
    
    /* Morph shape necessary with bubble or elastic */
    .bm-morph-shape {
      fill: #39322c;
    }
    
    /* Wrapper for item list */
    .bm-item-list {
      color: #ffffff;
      padding: 0.8em;
    }
    
      /* Individual item */
      .bm-item {
        display: inline-block;
      }
      
      /* Styling of overlay */
      .bm-overlay {
        background: rgba(0, 0, 0, 0.3);
      }

      /* Hide Normal Nav for Small Media Query */
      .nav-container {
        display: none;
      }

    }
.Footer {
